.sign-in__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: white;
}

.hr-container {
  display: flex;
  align-items: center;
  text-align: center;
  width: 90%;
}

.hr-container::before,
.hr-container::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid #4f4e4e;
}

.hr-container:not(:empty)::before {
  margin-right: .25em;
}

.hr-container:not(:empty)::after {
  margin-left: .15em;
}


@media (max-width: 768px) {
  .sign-in__wrapper {
    padding: 1rem;
  }

  .hr-container {
    width: 100%;
  }
}

.scrollable-container {
  max-height: calc(6 * 150px);
  overflow-y: scroll;
  overflow-x: hidden;
}

.scrollable-container::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.scrollable-container {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollable-container::-webkit-scrollbar-thumb {
  background: transparent;
}

.row-height {
  height: 100px;
}

.card {
  border: 1px solid black;
  border-radius: 0;

}


.card-body {
  padding: 1rem;
}

.card-title,
.card-text {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.card-image {
  width: 80px;
  height: auto;
  margin-right: 1rem;
  object-fit: contain;
}

.d-flex {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.flex-grow-1 {
  flex-grow: 1;
}

.mt-4 {
  margin-top: 1.5rem;
}

.text-center {
  text-align: center;
}

.copyright {
  text-align: center;
}

.header .navbar-nav {
  margin-left: auto;
}